import { createStore } from "vuex";

const store = createStore({
  state() {
    return {
      tasks: [],
    };
  },
  mutations: {
    addTask(state, task) {
      state.tasks.push(task);
    },
    deleteTask(state, index) {
      state.tasks.splice(index, 1);
    },
    editTask(state, { index, task }) {
      state.tasks[index] = task;
    },
  },
});

export default store;
