<template>
  <div>
    <h1>Список задач</h1>
    <form @submit.prevent="addTask">
      <input type="text" v-model="newTask" placeholder="Введите новую задачу" />
      <button type="submit">Добавить задачу</button>
    </form>
    <ul>
      <li v-for="(task, index) in tasks" :key="index">
        {{ task }}
        <button @click="editTask(index)">Редактировать</button>
        <button @click="deleteTask(index)">Удалить</button>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      newTask: "",
      tasks: ["Задача 1", "Задача 2"],
    };
  },
  methods: {
    addTask() {
      if (this.newTask.trim() !== "") {
        this.tasks.push(this.newTask);
        this.newTask = "";
      }
    },
    deleteTask(index) {
      this.tasks.splice(index, 1);
    },
    editTask(index) {
      const task = prompt("Введите новое название задачи", this.tasks[index]);
      if (task && task.trim() !== "") {
        this.tasks[index] = task;
      }
    },
  },
};
</script>

